import React from "react";
import { MDXProvider } from "@mdx-js/react";

import { Slide } from "./slide";
import { PSection } from "./psection";
import { TitleSlide } from "./titleslide";
import { Animate } from "./animate";
import { Example, Tooltip, Definition, Meta } from "./utils";
import { TwoCols, Flex, Card } from "./layout";

class Presentation extends React.Component {
  render() {
    const shortcodes = {
      Slide,
      TitleSlide,
      Animate,
      PSection,
      Example,
      Tooltip,
      Definition,
      TwoCols,
      Flex,
      Card,
      Meta
    };

    return (
      <div className="draft" style={{ width: "75ch"}}>
        <MDXProvider components={shortcodes}>{this.props.children}</MDXProvider>
      </div>
    );
  }
}

export default Presentation;
